import axios from 'axios'
import dotenv from 'dotenv'
dotenv.config()

const URL_BACKEND = "https://6096ac6f116f3f00174b3763.mockapi.io/api"



export const verificarExistenciaUsuario = (uid) => {
  const rpta = axios.get(`${URL_BACKEND}/user?uid=${uid}`);
  return rpta
}

export const buscarUsuarioPorEmail = (email) => {
  const rpta = axios.get(`${URL_BACKEND}/user?email_unitru=${email}`);
  return rpta
}

export const crearUsuario = async (userDataObject) => {
  const rpta = axios.post(`${URL_BACKEND}/user`,
    JSON.stringify(userDataObject),
    {
      headers: {
        "Content-type": "application/json"
      }
    }
  );
  return rpta
}

export const buscarUsuarios = () => {
  const rpta = axios.get(`${URL_BACKEND}/user`);
  return rpta
}
