import React from 'react';
import { Switch, Route } from 'react-router-dom';
import SilabosCreatePage from './pages/SilabosCreatePage';
import SilabosDetailPage from './pages/SilabosDetailPage';
import SilabosListPage from './pages/SilabosListPage';

const SilabosRouter = () => {
  return (
    <Switch>
      <Route path='/panel/silabos/detalle'>
        <SilabosDetailPage />
      </Route>
      <Route path='/panel/silabos/crear'>
        <SilabosCreatePage />
      </Route>
      <Route path='/panel/silabos/editar'>
        <SilabosCreatePage />
      </Route>
      <Route path='/panel/silabos'>
        <SilabosListPage />
      </Route>
    </Switch>
  );
}

export default SilabosRouter;
