import React, { useContext, useEffect, useState } from 'react';
import AuthContext from '../../../context/authContext';
import { auth, googleProvider } from '../../../services/firebase';
import { useHistory } from 'react-router-dom';
import { crearUsuario, verificarExistenciaUsuario } from '../../../services/authService';

const objUsuario = {
  uid: "",
  nombres: "",
  apellido_paterno: "",
  apellido_materno: "",
  foto_perfil: "",
  email_unitru: "",
  tipo_usuario: "",
  codigo: "",
  cargo: "",
  facultad: "",
  departamento: "",
  escuela: "",
  profesion: "",
  grado_academico: "",
  firma: ""
}


const AuthLoginScreen = () => {
  const user = useContext(AuthContext);
  const [redirect, setRedirect] = useState(null);
  const history = useHistory()

  useEffect(() => {
    if (user) {
      console.log(user);
      setRedirect('/panel')
    }
  }, [user])

  if (redirect) {
    history.push(redirect)
  }

  const signInWithGoogle = () => {
    auth.signInWithPopup(googleProvider).then((res) => {

      verificarExistenciaUsuario(res.user.uid).then(rpta => {

        if (rpta.data.length === 0) {
          const nombre_completo = res.user.providerData[0].displayName.split(" ")

          const nuevoUsuario = {
            ...objUsuario,
            nombres: nombre_completo.slice(0, -2).join(' '),
            apellido_materno: nombre_completo.slice(-1),
            apellido_paterno: nombre_completo.slice(-2, -1),
            uid: res.user.uid,
            email_unitru: res.user.providerData[0].email,
            foto_perfil: res.user.providerData[0].photoURL
          }

          crearUsuario(nuevoUsuario).then(rpta => {
            console.log(rpta);
          })
        } else {
          console.log('usuario ya existe');
        }
      })





    }).catch((error) => {
      console.log(error.message)
    })
  }

  return (
    <main className="login__vista">
      <figure className="portada">
        <img src={process.env.PUBLIC_URL + "assets/electronico.jpg"} alt="" />
      </figure>

      <div className="identificate">
        <figure>
          <img src={process.env.PUBLIC_URL + "/logo_mecatronica.png"} alt="" />
        </figure>
        <h1>Bienvenido al ERP de Ingeniería Mecatrónica</h1>
        <p>Identifiquese usando su correo:</p>

        <button className="ingresar" onClick={signInWithGoogle}>
          <img src={process.env.PUBLIC_URL + "/assets/icon__google.png"} alt="" />
            &nbsp;&nbsp; Ingresa con Google
        </button>
      </div>
    </main>
  );
}

export default AuthLoginScreen;
