import React from 'react';

const CardUser = ({ usuario }) => {

  return (
    <>
      {
        usuario ?
          <div className="card__user">
            <figure>
              <img src={usuario.foto_perfil} alt="" width="200" />
            </figure>
            <div className="user__datos">
              <div className="datos__docente">
                <h3><i className="fas fa-user-tie"></i>{usuario.nombres.split(' ').slice(-2, -1) + " " + usuario.apellido_paterno}</h3>
                <p><i className="fas fa-layer-group"></i>{usuario.tipo_usuario}<span>({usuario.codigo})</span></p>
                <p><i className="fas fa-university"></i>Fac. de {usuario.facultad}</p>
                <p><i className="fas fa-graduation-cap"></i>Dep. de {usuario.departamento}</p>
                <p><i className="fas fa-envelope-square"></i>{usuario.email_unitru}</p>
                {/* <p><i className="fas fa-phone-square-alt"></i>{usuario.telefono}</p> */}
                <div className="datos__actions">
                  <button className="editar"><i className="fa fa-edit" aria-hidden="true"></i></button>
                  <button className="eliminar"><i className="fa fa-trash-alt" aria-hidden="true"></i></button>
                </div>
              </div>
            </div>
          </div>
          :
          null
      }
    </>

  );
}

export default CardUser;
