import React from 'react';
import { Switch, Route } from 'react-router-dom';
import PanelPerfilScreen from './pages/perfil/PanelPerfilScreen';
import PanelPrincipalScreen from './pages/principal/PanelPrincipalScreen';
import PanelUsuariosScreen from './pages/usuarios/PanelUsuariosScreen';
import SilabosRouter from './submodules/silabos/SilabosRouter';

const PanelRouter = () => {
  return (
    <Switch>
      <Route path='/panel/silabos'>
        <SilabosRouter />
      </Route>

      <Route path='/panel/usuarios'>
        <PanelUsuariosScreen />
      </Route>

      <Route path='/panel/perfil'>
        <PanelPerfilScreen />
      </Route>

      <Route path='/panel'>
        <PanelPrincipalScreen />
      </Route>
    </Switch>
  );
}

export default PanelRouter;
