import React from 'react';

const HeaderPanel = () => {
  return (
    <header>
      <nav className="navbar">
        <div className="navbar__logo">
          <h1>MECATRÓNICA-UNT</h1>
          <figure>
            <img src={process.env.PUBLIC_URL + "/assets/logo_unt.png"} alt="" width="50" />
          </figure>
        </div>
        <div className="navbar__logo__derecha">
          <figure>
            <img src={process.env.PUBLIC_URL + "/assets/logo_mecatronica.png"} alt="" />
          </figure>
        </div>
      </nav>
    </header>
  );
}

export default HeaderPanel;
