import React, { useContext, useEffect, useState } from 'react';
import { NavLink, useHistory } from 'react-router-dom';
import AuthContext from '../../../../context/authContext';
import { buscarUsuarioPorEmail } from '../../../../services/authService';
import { auth } from '../../../../services/firebase';

const Sidebar = () => {
  const [autenticado, setAutenticado] = useState(true);
  const [datosUsuario, setDatosUsuario] = useState(null);


  const hist = useHistory();
  const user = useContext(AuthContext);

  if (!user) {
    setAutenticado(false)
    hist.push('/auth/login');
  }

  useEffect(() => {
    if (!autenticado) {
      hist.push('/');
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [autenticado]);

  const desconectar = () => {
    auth.signOut().then(() => {
      setAutenticado(false)

    }).catch((error) => {
      console.log(error.message)
    })
  }

  useEffect(() => {
    buscarUsuarioPorEmail(user.email).then(rpta => {
      setDatosUsuario(rpta.data[0])
    })
  }, [user.email]);



  return (
    <div className="panel__sidebar">
      <div className="sidebar__top">
        <div className="sidebar__perfil">
          <figure>
            <img src={datosUsuario ? datosUsuario.foto_perfil : null} alt="" />
          </figure>
          <h3 className="perfil__nombre">
            {
              datosUsuario ? datosUsuario.nombres.split(' ').slice(-2, -1) + " " + datosUsuario.apellido_paterno : null
            }
          </h3>
          <p className="perfil__departamento">{datosUsuario ? datosUsuario.departamento : null}</p>
          <p className="perfil__categoria">{datosUsuario ? datosUsuario.tipo_usuario : null}</p>
        </div>

        <ul className="sidebar__menu">
          <NavLink to='/panel' exact activeClassName="seleccionado" component='li'> <i className="fas fa-th-large"></i> <NavLink to='/panel'>Panel de control</NavLink>
          </NavLink>
          <NavLink to='/panel/perfil' activeClassName="seleccionado" component='li'> <i className="fas fa-user-circle"></i> <NavLink to='/panel/perfil'>Perfil de
              usuario</NavLink></NavLink>
          {/* className="no-activo" */}
          <NavLink to='/panel/usuarios' activeClassName="seleccionado" component='li' > <i className="fas fa-users"></i> <NavLink to="/panel/usuarios">Cuentas de usuario</NavLink></NavLink>
          <li> <i className="fas fa-sign-out-alt"></i> <NavLink to='/' onClick={desconectar}>Salir</NavLink> </li>
        </ul>
      </div>

      <div className="sidebar__bottom">
        <div className="sidebar__acciones">
          <button className="acciones__administrador">
            IR A VISTA ADMINISTRADOR
          </button>
          <button className="acciones__usuario no-activo">
            IR A VISTA USUARIO
          </button>
        </div>

        <div className="sidebar__copyright">
          <small className="copyright__autor">Diseñado y elaborado por <a href="https://eastor112.github.io/"
            target="_blank" rel='noreferrer'>eastor112</a></small>
          <small className="copyright__simbolo">Copyright 2021 <i className="fa fa-copyright" aria-hidden="true"></i></small>
        </div>
      </div>

    </div>
  );
}

export default Sidebar;
