import React from 'react';
import './styles.scss';
import {
  HashRouter as Router,
  Switch,
  Route
} from "react-router-dom";
import AuthRouter from './modules/auth/AuthRouter';
import IndexRouter from './modules/index/IndexRouter';
import PanelRouter from './modules/panel/PanelRouter';
import AuthState from './context/authState';

const App = () => {
  return (
    <Router>
      <AuthState>
        <Switch>
          <Route path="/auth">
            <AuthRouter />
          </Route>
          <Route path="/panel">
            <PanelRouter />
          </Route>
          <Route path="/">
            <IndexRouter />
          </Route>
        </Switch>
      </AuthState>
    </Router>
  );
}

export default App;
